.container-content{
    display: flex;
    width: 215px;
    height: 290px;
    margin: 0 auto;
    border-radius: 7px;
    box-shadow: 1px 1px 1px 1px rgb(0 0 0 / 10%);
    float: right;
    padding: 0px 0px 37px 0px;
    margin-right: 10px;
    background-color: #fff;;
}

.card{
    height: 180px;
    display: flex;
    justify-content: center;
    align-items: center;
    object-fit: contain;
    background-color: currentcolor;
    width: 215px;
    border-radius: 7px 7px 0px 0px;

}

.content-card{
    height: fit-content;
    margin-top: 182px;
    position: absolute;
    width: fit-content;
    padding: 3px 8px;
    font-family: arial;
    font-weight: bold;
}

.btn-content{
    margin-top: 280px;
    position: absolute;
    margin-left: 8px;
    width: 100%;
}
.contenido{
    width: 200px;
    font-size: 11px;
    margin-top: 20px;
    font-weight: 500;
    color: #4e4c4c;
}